<script setup>

import {computed, onMounted} from "vue";
import {useStore} from "vuex";

const store = useStore()
const states = computed(() => store.getters.states)

onMounted(() => {
	const $paths = document.querySelectorAll('.cover-map g > path')
	const $stateName = document.querySelector('.state-name')
	$paths.forEach($path => {
		$path.addEventListener('mousemove', (e) => {
			const id = $path.getAttribute('id')
			const deathCount = states.value.find(item => item.id === id).death_count
			$stateName.innerHTML = `<h3 class="font-weight-regular">${$path.getAttribute('title')}</h3> Death Count: ${deathCount}` ?? ''
			$stateName.style.left = e.clientX?.toString() + 'px'
			$stateName.style.top = e.clientY?.toString() + 'px'
		})
		$path.addEventListener('mouseleave', () => {
			// $stateName.style.display = 'none'
		})
	})
})

</script>
<style lang="scss">
.clip-map, .cover-map {
	height: 593px;
	width: 654px;
	z-index: 0;
	position: absolute;
	top: 0;
	left: 0;
}
.cover-map {
	fill: transparent;
	stroke: #000;
	z-index: 3;
	& > g > path {
		stroke: #000;
		&:hover {
			stroke: #222;
			fill: #00000010;
		}
	}
}
.state-name {
	position: fixed;
	background: rgb(255, 255, 255);
	z-index: 3;
	color: red;
	padding: 4px 8px;
	border-radius: 4px;
	font-weight: 700;
	box-shadow: 0 5px 9px rgb(0, 0, 0, 0.25);
	transition: all 1200ms ease-in-out;
}
</style>
